import { SHOW_LOADING, HIDE_LOADING, SET_FORM_DATA, SET_MODULE_DATA, TOGGLE_SUB_MODULE,  TOGGLE_SUB_MODULE_FORM, ACTIVE_SUB_MODULE } from './app.types';


export const showLoader = () => ({type: SHOW_LOADING });
export const hideLoader = () => ({type: HIDE_LOADING });
export const formData = (formData) => ({type: SET_FORM_DATA , formData});
export const moduleData = (moduleData) => ({type: SET_MODULE_DATA , moduleData});
export const toggleSubModule = (value) => ({type: TOGGLE_SUB_MODULE , value});
export const toggleSubModuleForm = (value) => ({type: TOGGLE_SUB_MODULE_FORM , value});
export const activeSubModule = (value) => ({type: ACTIVE_SUB_MODULE , value});
