import React from 'react';
import { OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faTrash, faEdit, faList } from '@fortawesome/free-solid-svg-icons';
import PropTypes from 'prop-types';

export const ActionIcons = React.memo(({ data, handlRowClick, ...props }) => {
    return (
        <div className="grid-action-icon">
            {props.view && (
                <OverlayTrigger
                    overlay={ <Tooltip id="tooltip-view">View</Tooltip> }
                >
                <FontAwesomeIcon icon={faEye} onClick={() => handlRowClick(data, "view")} />
                </OverlayTrigger>)
            }
            {props.edit && (
                <OverlayTrigger
                    overlay={ <Tooltip id="tooltip-edit">Edit</Tooltip> }
                >
               <FontAwesomeIcon icon={faEdit} onClick={() => handlRowClick(data, "edit")} />
                </OverlayTrigger>)
            }
            {props.list && (
                <OverlayTrigger
                    overlay={ <Tooltip id="tooltip-list">List</Tooltip> }
                >
                <FontAwesomeIcon icon={faList} onClick={() => handlRowClick(data, "list")} />
                </OverlayTrigger>)
            }
            {props.delete && (
                <OverlayTrigger
                    overlay={ <Tooltip id="tooltip-delete">Delete</Tooltip> }
                >
                <FontAwesomeIcon icon={faTrash} onClick={() => handlRowClick(data, "delete")} />
                </OverlayTrigger>)
            }            
        </div>
    )
})


export const ActionButtons = React.memo(({ data, handlRowClick, ...props }) => {
    console.log("Render action button")
    return (
        <div className="grid-action-icon">
            {props.view && (
                <Button variant="info" size="sm" onClick={() => handlRowClick(data, "view")}>
                    View
              </Button>)  
            }
            {props.edit && (
                <Button variant="warning" size="sm" onClick={() => handlRowClick(data, "edit")}>
                        Edit
                </Button>)
            }
            {props.delete && (
               <Button variant="danger" size="sm" onClick={() => handlRowClick(data, "delete")}>
                        Delete
                </Button>)
            }
        </div>
    )
})
ActionIcons.propTypes = {
    data: PropTypes.object,
    handlRowClick : PropTypes.func
}

ActionButtons.propTypes = {
    data: PropTypes.object,
    handlRowClick : PropTypes.func
}
