
import { Link } from "react-router-dom";
import { useState, useCallback, useEffect } from 'react';
import {useDispatch} from 'react-redux';
import ReactDataGrid from '@inovua/reactdatagrid-community';
import {Row, Col, Card, Button} from 'react-bootstrap';
import {ActionIcons} from '../../components/common/GridAction';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Formik, Form, Field } from 'formik';
import {getDepartments} from '../../api/account.api';
import {formData} from '../../store/modules/application/app.action';
import { showLoader, hideLoader } from '../../store/modules/application/app.action';
import HTTP from '../../service/http';
import Notify from '../../utils/notify';
const lang = require('../../language/en.json');

const Department = () => {
    const dispatch = useDispatch();

    const [panelWidth] = useState({left: 8, right: 4});
    const [gridRef, setGridRef] = useState(null);
    const [enableEditing, setEnableEditing] = useState(false);
    const [formValues, setFormValues] = useState({});
    
    const columns = [
        { name: 'id', header: 'S.No', minWidth: 50, defaultFlex: 1 },
        { name: 'name', header: 'Department Name', maxWidth: 200, defaultFlex: 1 },    
        { name: 'type', header: 'Department Type', maxWidth: 200, defaultFlex: 1 },
        { name: '', header: 'Action', defaultFlex: 1, sortable: false, render: (({ data }) => <ActionIcons data={data} handlRowClick={handlRowClick}  edit/>) }
    ]

    const gridStyle = { minHeight: 400 }
        
    const loadData = ({ skip, limit, sortInfo, groupBy, filterValue }) => new Promise((resolve, reject) => {    

        let searchText = filterValue!==null?filterValue[0].value:"";
        getDepartments().then(resp => {  
            return resolve({count: resp.data.response.numberOfElements, data: resp.data.response})
        }).catch(e => {      
            return resolve({count: 0, data: []})
        })
    })
    
    const handlRowClick = useCallback((data, action) => {   
        dispatch(formData(data));
        
        if(action==="edit"){ 
            setEnableEditing(true)
            setFormValues(data)
          }
    },[]);

    const dataSource = useCallback(loadData, []);

    const onCreateSuccess =(data)=> {      
        gridRef.current.reload()    
        console.log("Recored added, append to list", data);
    }

    const [initialValues] = useState({ name: '', type: '', active:'' })
    const [initialDataValues, setInitialDataValues] = useState({})
    
    useEffect(() => {
        if(enableEditing){            
          setInitialDataValues(formValues)
        }
    },[enableEditing, formValues])

   
    
    const onSubmit = async (values, {resetForm}) => {
        
        const errors = {};
        if (!values.name) {
            errors.name = alert("Department Name is required");
            return false;
        } 
        if (!values.type) {
            errors.type = alert("Department Code is required");
            return false;
        } 
        try {
            dispatch(showLoader());
            const result = await HTTP.Request((values.id===undefined) ? "POST" : "PUT", process.env.REACT_APP_API_URL+"department", values);
            onCreateSuccess(result);
            dispatch(hideLoader());
            Notify.success(result.data.message);
            resetForm({initialValues});
        } catch (err) {      
            dispatch(hideLoader());
            let message = (err.message===undefined) ? err: err.message;
            Notify.error(message);
        }   
    }
    return (
        <>
            <div className="container">
                <nav aria-label="breadcrumb">
                    <ol className="breadcrumb">
                        <li className="theme-font-size breadcrumb-item"><Link className="theme-color" to="/dashboard">{lang.home}</Link></li>
                        <li className="theme-font-size breadcrumb-item active" aria-current="page">{lang.department}</li>
                    </ol>
                </nav>
            </div>
            <Row>
            <Col md={{span: panelWidth.left}}>
                <Card>
                <Card.Header>
                {lang.tableRecords}
                </Card.Header>
                <Card.Body> 
                    <ReactDataGrid
                    onReady={setGridRef}
                    idProperty="id"
                    style={gridStyle}
                    columns={columns}
                    // pagination
                    dataSource={dataSource}
                    //defaultLimit={10}
                    />
                </Card.Body>
                </Card>
            </Col>
            <Col md={{span: panelWidth.right}}>
                <Card>
                <Card.Header>
                {lang.departmentForm} 
                </Card.Header>
                <Card.Body>
                <div>
                    <InfiniteScroll
                    dataLength={60}
                    height={400}
                    >
                    <Formik
                    initialValues={enableEditing ? initialDataValues : initialValues}
                    enableReinitialize={true}
                    onSubmit={onSubmit}>
                        {formik => (
                        <Form>
                            <div className='form-group'>
                                <label className="theme-font-size" htmlFor="name">{lang.departmentname}</label> 
                                <Field  id="name" className="form-control"  name="name" placeholder="Department Name" />
                            </div>
                            <div className='form-group'>
                                <label className="theme-font-size" htmlFor="type">{lang.departmentcode}</label> 
                                <Field as="select" name="type" id='type' className='form-control'>
                                    <option value="">---</option>
                                    <option value="A">{lang.academics}</option>
                                    <option value="O">{lang.organization}</option>
                                </Field>
                            </div>
                            <Button className="theme-back-color" variant="primary" type="submit">{lang.save}</Button>
                        </Form>
                        )}
                    </Formik>
                    </InfiniteScroll> 
                </div>
                </Card.Body>
                </Card> 
            </Col> 
            </Row>
        </>
    )
}
export default Department;